<template>
  <div class="public-box">
    <div class="head-box">
      <el-button-group class="head-tab">
          <el-button id="hot_b" round class="head-item" v-on:click="hot" @click="block('1')">吹牛大会</el-button>
          <el-button id="recommend_b" round class="head-item" v-on:click="recommend" @click="block('2')">故事分享会</el-button>
      </el-button-group>
    </div>
    <div class="edit-box">
      <el-form :model="textForm" label-width="0px" ref="publicrefs">
        <el-form-item prop="title" class="title-box">
          <el-input v-model="textForm.title" class="title-input" placeholder="文章标题（必填）"></el-input>
        </el-form-item>
        <el-form-item prop="content" class="text-box">
          <el-input v-model="textForm.content" class="text-input" :rows="15" type="textarea"></el-input>
        </el-form-item>
        <el-form-item class="text-submit">
          <el-button class="iconfont iconchenggong1" type="warning primary" round v-on:click="submit"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <tab-bar class="app-bottom"></tab-bar>
  </div>
</template>

<script>
import tabBar from './tabbar/tabBar.vue'
export default {
  name: 'public',
  components: {
    tabBar
  },
  data () {
    return {
      textForm: {
        title: '',
        content: ''
      }
    }
  },
  methods: {
    hot () {
      hot_b.style.backgroundColor = '#fcaa45'
      hot_b.style.color = '#fff'
      recommend_b.style.backgroundColor = '#fff'
      recommend_b.style.color = '#fcaa45'
    },
    recommend () {
      hot_b.style.backgroundColor = '#fff'
      hot_b.style.color = '#fcaa45'
      recommend_b.style.backgroundColor = '#fcaa45'
      recommend_b.style.color = '#fff'
    },
    block (type) {
      switch (type) {
        case '1':
          return '1'
        case '2':
          return '2'
      }
    },
    submit () {
      const line = this.block (type)
      console.log(line)
      switch (line) {
        case '1':
          this.$refs.publicrefs.validate(async valid => {
            if(!valid) return
            const temp = await this.$http.post("article/swank", [this.textForm, token])
            if(temp.status !== 200) return this.$message.error('发布失败')
            this.$router.push('/public')
            return this.$message.success('发布成功')
        })
        case '2':
          this.$refs.loginrefs.validate(async valid => {
          if(!valid) return
          const temp = await this.$http.post("article/story", this.textForm)
          if(temp.status !== 200) return this.$message.error('发布失败')
          this.$router.push('/public')
          return this.$message.success('发布成功')
        })
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
  .public-box
    background: url('~@/assets/pic/public.png') center center no-repeat
    background-attachment:fixed
    background-size 100% 100%
    background-color rgb(99,148,183)
    width 100%
    height 100%
    font-size .5rem
    .head-box
      position absolute
      top 8%
      width 100%
      display flex
      flex-direction row
      background-size contain
      .head-tab
        width 80%
        position absolute
        top 100%
        left 50%
        transform translate(-50%,-50%)
        .head-item
          width 50%
          height .36rem
          padding 0px
          color #fcaa45
          font-size .18rem
          text-align center
          border 1px solid #fcaa45
          box-shadow 0px 3px 5px rgb(205,205,205)
    .edit-box
      position relative
      top 15%
      .title-box
        margin .2rem 11%
        margin-top 0
        .title-input
          opacity 0.5
          color #fff
      .text-box
        margin .2rem 2%
        .text-input
          border-radius .06rem
          opacity 0.5
          font-size .2rem
      .text-submit
        position absolute
        bottom 14%
        right 8%
      button.el-button
        width .7rem
        height .7rem
        border none 
        border-radius .35rem
        box-shadow 0px 2px 3px grey
        position relative
        top 80%
        left 50%
        transform translate(-50%, 90%)
        background-color #fcaa45
        color #ffffff
        font-size .3rem
    .app-bottom
      position fixed
      bottom 0px
      z-index 99
      width 100%
</style>


