<template>
  <div class="mainpage">
    <div class="head-static-box">
      <div class="head-box">
        <el-button-group class="head-tab">
            <el-button id="hot_b" round class="head-item" v-on:click="hot">热门</el-button>
            <el-button id="recommend_b" round class="head-item" v-on:click="recommend">推荐</el-button>
        </el-button-group>
      </div>
      <div class="head-title-hot" v-show="show_hot">
        <img src="@/assets/pic/title_left.png">
        那些把牛吹上天的人
        <img src="@/assets/pic/title_right.png">
      </div>
      <div class="head-title-recommend" v-show="show_recommend">
        推荐有趣的人给你
        <span class="more_people iconfont icongengduo"></span>
      </div>
    </div>
    <!--包含文章的块-->
    <div>
      <!--热门块-->
      <hot v-show="show_hot"></hot>
      <!--推荐块-->
      <recommend v-show="show_recommend"></recommend>
    </div>
    <tab-bar class="app-bottom"></tab-bar>
  </div>
</template>

<script>
import tabBar from './tabbar/tabBar.vue'
import hot from './components/hot.vue'
import recommend from './components/recommend.vue'
export default {
  name: 'mainPage',
  components: {
    tabBar,
    hot,
    recommend
  },
  data () {
    return {
      show_hot: true,
      show_recommend: false
    }
  },
  methods: {
    hot () {
      this.show_hot = true
      this.show_recommend = false
      hot_b.style.backgroundColor = '#64b7fe'
      hot_b.style.color = '#fff'
      recommend_b.style.backgroundColor = '#fff'
      recommend_b.style.color = '#64b7fe'
    },
    recommend () {
      this.show_recommend = true
      this.show_hot = false
      hot_b.style.backgroundColor = '#fff'
      hot_b.style.color = '#64b7fe'
      recommend_b.style.backgroundColor = '#64b7fe'
      recommend_b.style.color = '#fff'
    }
  }
}
</script>

<style lang="stylus" scoped>
  .mainpage
    background: url('~@/assets/pic/my.png') center center no-repeat
    background-attachment:fixed
    background-size 100% 100%
    background-color rgb(99,148,183)
    width 100%
    height 100%
    font-size .5rem
    .head-static-box
      position relative
      height 1.56rem
      width 100%
      .head-box
        position absolute
        top 40%
        width 100%
        display flex
        flex-direction row
        background-size contain
        .head-tab
          width 40%
          position absolute
          top 100%
          left 50%
          transform translate(-50%,-50%)
          .head-item
            width 50%
            height .36rem
            padding 0px
            color #64b7fe
            font-size .18rem
            text-align center
            box-shadow 0px 3px 5px rgb(205,205,205)
      .head-title-hot
        padding-top .1rem
        padding-bottom .1rem
        position relative
        top 65%
        height .28rem
        width 100%
        background-color #ffffff
        color rgb(100,183,254)
        text-align center
        font-size .18rem
        box-shadow 0px 0px 5px grey
      .head-title-recommend
        padding-top .14rem
        padding-bottom .14rem
        padding-left .05rem
        box-sizing border-box
        position relative
        top 65%
        height .48rem
        width 100%
        background-color #ffffff
        color rgb(100,183,254)
        text-align left 
        font-size .18rem
        box-shadow 0px 0px 5px grey
        .more_people
          color #64b7fe
          position absolute
          right 1%
          font-size .2rem
          text-align right 
    .app-bottom
      position fixed
      bottom 0px
      z-index 99
      width 100%
    .main-top-tab
      position relative
      top 8%
      z-index 99
      width 100%
      left 50%
      transform translate(-50%,0)
</style>


