<template>
  <div class="login_c">
    <div class="login_box">
      <!--登录表单区域-->
      <el-form :model="loginForm" :rules="loginRules" ref="loginrefs" label-width="0px">
        <el-form-item prop="username">
          <el-input prefix-icon="iconfont iconshenfenzheng" v-model="loginForm.username" placeholder="非常抱歉，功能开发中"></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input prefix-icon="iconfont iconmima" v-model="loginForm.checkword" placeholder="请返回注册或登录"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button class="iconfont" type="primary" round @click="logincheck">&#xe666</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import {setCookie,getCookie} from '@/assets/js/cookie.js'
export default {
  name: 'Login',
  data () {
    return {
      //登陆表单数据绑定对象
      loginForm: {
        username: '',
        checkword: ''
      },
      //表单验证规则对象
      loginRules: {
        //验证是否输入用户名
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' }
        ],
        //验证验证码是否合法
        password: [
          { required: true, message: '请输入验证码', trigger: 'blur' },
          { min: 6, max: 6, message: '验证码错误', trigger: 'blur' }
        ]
      }
    }
  },
  //存储登录数据，如果有登录数据则直接登录
  mounted () {
    if(getCookie('username')) {
      this.$router.push('/mainpage')
    }
  },
  methods: {
    logincheck () {
      this.$refs.loginrefs.validate(async valid => {
        if(!valid) return
        const {data: res} = await this.$http.post("login", this.loginForm)
        console.log(res)
      })
    },
    toRegister () {
      this.$router.push('/register')
    },
    toFound () {
      this.$router.push('/toFound')
    }
  }
}
</script>

<style lang="stylus" scoped>
  .login_c
    background: url('~@/assets/pic/login.png') center center no-repeat
    background-size 100% 100%
    background-color rgb(99,148,183)
    width 100%
    height 100%
    font-size .5rem
    .login_box
      width 68%
      height 2.43rem
      position absolute
      left 50%
      top 40%
      transform translate(-50%, -50%)
      button.el-button
        width .8rem
        height .8rem
        border-radius .4rem
        position relative
        top 80%
        left 50%
        transform translate(-50%, 90%)
        background-color rgb(142,180,210)
        font-size .3rem
</style>


