<template>
  <div style="background-color: #fff">
    <div class="bottom-box">
      <!--首页-->
      <div class="img-box" @click="ontouch('1')">
        <img v-if="typeclick=='1'" src="@/assets/pic/bottombar/main-2.png" alt="首页">
        <img v-else src="@/assets/pic/bottombar/main.png" alt="首页">
        <div class="img-desc">首页</div>
      </div>
      <!--广场-->
      <div class="img-box" @click="ontouch('2')">
        <img v-if="typeclick=='2'" src="@/assets/pic/bottombar/playground-2.png" alt="广场">
        <img v-else src="@/assets/pic/bottombar/playground.png" alt="广场">
        <div class="img-desc">广场</div>
      </div>
      <!--发布-->
      <div class="img-box" @click="ontouch('3')">
        <img v-if="typeclick=='3'" src="@/assets/pic/bottombar/public-2.png" alt="发布">
        <img v-else src="@/assets/pic/bottombar/public.png" alt="发布">
        <div class="img-desc">发布</div>
      </div>
      <!--我的-->
      <div class="img-box" @click="ontouch('4')">
        <img v-if="typeclick=='4'" src="@/assets/pic/bottombar/my-2.png" alt="我的">
        <img v-else src="@/assets/pic/bottombar/my.png" alt="我的">
        <div class="img-desc">我的</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'tabBar',
  data () {
    return {
      typeclick: '2'
    }
  },
  methods: {
    ontouch (type) {
      this.typeclick = type
      switch(type) {
        case '1': 
          this.$router.replace("/mainPage")
          break
        case '2': 
          this.$router.replace("/playGround")
          break
        case '3':
          this.$router.replace("/public")
          break
        case '4':
          this.$router.replace("/my")
          break
        default: break
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
  .bottom-box
    width 100%
    height .67rem
    display flex
    flex-direction row
    background-size contain
    box-shadow 0px 0px 10px grey
    .img-box
      width 25%
      height 100%
      display flex
      flex 1
      flex-direction column
      align-items center
      justify-content center
      color grey
    .img-desc
      padding-top .1rem
      font-size .05rem
      text-align center
      background-repeat no-repeat
      background-size contain
</style>


