<template>
  <div class="playground-box">
    <div class="head-static-box">
      <div class="head-box">
        <el-button-group class="head-tab">
            <el-button id="hot_b" round class="head-item" v-on:click="hot">吹牛大会</el-button>
            <el-button id="recommend_b" round class="head-item" v-on:click="recommend">故事分享会</el-button>
        </el-button-group>
      </div>
    </div>
    <div>
      <!--热门块-->
      <story v-show="show_hot"></story>
      <!--推荐块-->
      <lie v-show="show_recommend"></lie>
    </div>
    <tab-bar class="app-bottom"></tab-bar>
  </div>
</template>

<script>
import tabBar from './tabbar/tabBar.vue'
import story from './components/story.vue'
import lie from './components/lie.vue'
export default {
  name: 'playGround',
  components: {
    tabBar,
    story,
    lie
  },
  data () {
    return {
      show_hot: true,
      show_recommend: false
    }
  },
  methods: {
    hot () {
      this.show_hot = true
      this.show_recommend = false
      hot_b.style.backgroundColor = '#ffaecf'
      hot_b.style.color = '#fff'
      recommend_b.style.backgroundColor = '#fff'
      recommend_b.style.color = '#ffaecf'
    },
    recommend () {
      this.show_recommend = true
      this.show_hot = false
      hot_b.style.backgroundColor = '#fff'
      hot_b.style.color = '#ffaecf'
      recommend_b.style.backgroundColor = '#ffaecf'
      recommend_b.style.color = '#fff'
    }
  }
}
</script>

<style lang="stylus" scoped>
  .playground-box
    background: url('~@/assets/pic/public.png') center center no-repeat
    background-attachment:fixed
    background-size 100% 100%
    background-color rgb(99,148,183)
    width 100%
    height 100%
    font-size .5rem
    .head-static-box
      position relative
      height .8rem
      width 100%
      .head-box
        position absolute
        top 70%
        width 100%
        display flex
        flex-direction row
        background-size contain
        .head-tab
          width 80%
          position absolute
          top 100%
          left 50%
          transform translate(-50%,-50%)
          .head-item
            width 50%
            height .36rem
            padding 0px
            color #ffaecf
            font-size .18rem
            text-align center
            border 1px solid #ffaecf
            box-shadow 0px 3px 5px rgb(205,205,205)
    .app-bottom
      position fixed
      bottom 0px
      z-index 99
      width 100%
</style>


