import Vue from 'vue'
import {Form, FormItem, Button, Input,ButtonGroup} from 'element-ui'
import Router from 'vue-router'
import Login from '@/pages/login/Login.vue'
import register from '@/pages/register/register.vue'
import toFound from '@/pages/tofound/toFound.vue'
import mainPage from '@/pages/mainpage/mainPage.vue'
import playGround from '@/pages/playground/playGround.vue'
import puBlic from '@/pages/public/puBlic.vue'
import my from '@/pages/my/my.vue'
import messageEdit from '@/pages/messageedit/messageEdit.vue'
import VueRouter from 'vue-router'
import Vuex from 'vuex'

Vue.use(Router)
Vue.use(Form)
Vue.use(FormItem)
Vue.use(Input)
Vue.use(Button)
Vue.use(ButtonGroup)
Vue.use(VueRouter)
Vue.use(Vuex)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    }, {
      path: '/register',
      name: 'register',
      component: register
    }, {
      path: '/toFound',
      name: 'toFound',
      component: toFound
    }, {
      path: '/mainPage',
      name: 'mainPage',
      component: mainPage
    }, {
      path: '/playGround',
      name: 'playGround',
      component: playGround
    }, {
      path: '/puBlic',
      name: 'puBlic',
      component: puBlic
    }, {
      path: '/my',
      name: 'my',
      component: my
    }, {
      path: '/messageEdit',
      name: 'messageEdit',
      component: messageEdit
    }
  ]
})

router.beforeEach((to, from, next) => {
  if(to.path === '/') return next()
  const tokenstr = window.sessionStorage.getItem('token')
  if(!tokenstr) return next('/')
  next()
})

export default router



