<template>
  <div class="login_c">
    <div class="login_box">
      <!--登录表单区域-->
      <el-form :model="loginForm" :rules="loginRules" ref="loginrefs" label-width="0px">
        <el-form-item prop="username">
          <el-input prefix-icon="iconfont iconshenfenzheng" v-model="loginForm.username" placeholder="请输入用户名"></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input prefix-icon="iconfont iconmima" v-model="loginForm.password" placeholder="请输入密码" type="password"></el-input>
        </el-form-item>
        <div class="code">
          <span class="code_c" v-on:click="toFound">忘记密码？点此找回</span>
        </div>
        <el-form-item>
          <el-button class="iconfont" type="primary" round @click="logincheck">&#xe666</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="register">
      <span class="register_c" v-on:click="toRegister">还没有账号？点击这里进行注册</span>
    </div>
  </div>
</template>

<script>
import '@/assets/style/login.css'
import { mapMutations } from 'vuex'
export default {
  name: 'Login',
  data () {
    return {
      //登陆表单数据绑定对象
      loginForm: {
        username: '',
        password: ''
      },
      //表单验证规则对象
      loginRules: {
        //验证是否输入用户名
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' }
        ],
        //验证密码是否合法
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    logincheck () {
      this.$refs.loginrefs.validate(async valid => {
        if(!valid) return
        const temp = await this.$http.post("account/token", this.loginForm)
        if(temp.status !== 200) return this.$message.error('登陆失败')
        window.sessionStorage.setItem("token", temp.data)
        this.$router.push('/mainPage')
        return this.$message.success('登陆成功')
      })
    },
    toRegister () {
      this.$router.push('/register')
    },
    toFound () {
      this.$router.push('/toFound')
    }
  }
}
</script>

<style lang="stylus" scoped>
  .login_c
    background: url('~@/assets/pic/login.png') center center no-repeat
    background-size 100% 100%
    width 100%
    height 100%
    font-size .5rem
    .login_box
      width 68%
      height 2.43rem
      position absolute
      left 50%
      top 40%
      transform translate(-50%, -50%)
      .code
        width 100%
        position absolute
        top 50%
        .code_c
          position absolute
          transform translate(-50%)
          left 50%
          width 100%
          font-size .1rem
          color #ffffff
          text-align center
          text-decoration underline
      button.el-button
        width .8rem
        height .8rem
        border none 
        border-radius .4rem
        box-shadow 0px 2px 3px grey
        position relative
        top 80%
        left 50%
        transform translate(-50%, 90%)
        background-color rgba(255,255,255,0.5)
        color grey
        font-size .3rem
      input.el-input__inner 
        border-radius .2rem
        background-color rgb(128,168,200)
    .register
      width 100%
      position absolute
      top 90%
      .register_c
        position absolute
        transform translate(-50%)
        left 50%
        width 100%
        font-size .1rem
        color #ffffff
        text-align center
        text-decoration underline 
</style>


