<template>
  <div class="m-edit-box">
    <div class="m-head">
      <span class="el-icon-arrow-left back"></span>
    </div>
    <div class="img-all">
      <div class="img-box">
        <img src="@/assets/pic/test-img/2.png" alt="头像">
      </div>
      <div class="set-link iconfont icongengduo"></div>
    </div>
    <div class="t-box">
      <el-form label-width="60px">
        <el-form-item label="昵称">
          <el-input></el-input>
        </el-form-item>
        <el-form-item label="生日">
          <el-input></el-input>
        </el-form-item>
        <el-form-item label="介绍">
          <el-input type="textarea" :rows="5"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button class="iconfont iconchenggong1" type="primary" round></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'messageEdit'
}
</script>

<style lang="stylus" scoped>
  .m-edit-box
    background: url('~@/assets/pic/message.png') center center no-repeat
    background-attachment:fixed
    background-size 100% 100%
    background-color rgb(99,148,183)
    width 100%
    height 100%
    font-size .5rem
    .m-head
      width 100%
      height .7rem
      background-color rgba(255,255,255,0.5)
      box-shadow 0px 3px 5px rgb(205,205,205)
      font-size .4rem
      .back
        margin .15rem 0
        color #5a5959
        font-weight bold
    .img-all
      position relative
      top 3%
      width 100%
      height .92rem
      display flex
      .img-box
        padding-top .11rem
        padding-left .2rem
        width .7rem
        height .7rem
        border-radius .35rem
        overflow hidden
      .set-link
        position absolute
        right 0%
        font-size .25rem
        font-weight bold
        padding-right .15rem
        padding-top .35rem
        color rgb(205,205,205)
    .t-box
      margin 0 auto
      width 94%
      height 2.43rem
      position relative
      top 8%
      line-height .7rem
      button.el-button
        width .8rem
        height .8rem
        border none 
        border-radius .4rem
        box-shadow 0px 2px 3px grey
        position relative
        top 95%
        left 50%
        transform translate(-50%, 110%)
        background-color rgb(171,216,141)
        font-size .3rem
</style>


