<template>
  <div class="login_c">
    <div class="login_box">
      <!--登录表单区域-->
      <el-form class="login_input" :model="registerForm" :rules="loginRules" ref="loginrefs" label-width="0px">
        <el-form-item prop="username">
          <el-input prefix-icon="iconfont iconshenfenzheng" v-model="registerForm.username" placeholder="请输入用户名"></el-input>
        </el-form-item>
        <el-form-item prop="email">
          <el-input prefix-icon="el-icon-message" v-model="registerForm.email" placeholder="请输入邮箱地址"></el-input>
          <button class="check-b">发送验证码</button>
        </el-form-item>
        <el-form-item prop="password">
          <el-input prefix-icon="iconfont iconmima" v-model="registerForm.password" placeholder="请输入密码" type="password"></el-input>
        </el-form-item>
        <el-form-item prop="again">
          <el-input prefix-icon="iconfont iconmima" v-model="registerForm.again" placeholder="请确认密码" type="password" @blur="check"></el-input>
        </el-form-item>
        <el-form-item prop="checkcode">
          <el-input prefix-icon="iconfont iconmima" v-model="registerForm.checkcode" placeholder="请输入验证码" type="number"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button class="iconfont" type="primary" round @click="register">&#xe666</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="register">
      <span class="register_c" v-on:click="toLogin">已经注册？点击这里返回登录</span>
    </div>
  </div>
</template>

<script>
import {setCookie,getCookie} from '@/assets/js/cookie.js'
export default {
  name: 'register',
  data () {
    return {
      //登陆表单数据绑定对象
      registerForm: {
        username: '',
        password: '',
        again: '',
        email: '',
        checkcode: ''
      },
      //表单验证规则对象
      loginRules: {
        //验证是否输入用户名
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' }
        ],
        //验证密码是否合法
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
        ],
        //验证是否为正确的邮箱
        email: [
            {required: true, message: '请输入邮箱地址', trigger: 'blur' },
            { type: 'email', message: '请输入正确的邮箱地址', trigger: 'blur,change' }
        ],
        checkcode: [
          { required: true, message: '请输入验证码', trigger: 'blur' },
          { min: 6, max: 6, message: '长度为 6 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    check () {
      if(this.password !== this.again) alert('两次密码不一致')
    },
    register (registerForm) {
      this.check()
      this.$refs[registerForm].validate(async valid => {
        if(!valid) return alert('如果注册失败，请联系后端直接添加账号')
        const {message: res} = await this.$http.post("account/user", this.registerForm)
        if(res !== "注册成功") return this.$message.error('如果注册失败，请联系后端直接添加账号');
        return this.$message.success('注册成功')
        this.$router.push('/')
      })
    },
    toLogin () {
      this.$router.push('/')
    }
  }
}
</script>

<style lang="stylus" scoped>
  .login_c
    background: url('~@/assets/pic/login.png') center center no-repeat
    background-size 100% 100%
    background-color rgb(99,148,183)
    width 100%
    height 100%
    font-size .5rem
    .login_box
      width 68%
      height 3.57rem
      position absolute
      left 50%
      top 40%
      transform translate(-50%, -50%)
      .login_input
        font-size .3rem
        .check-b
          background-color rgba(255,255,255,0.5)
          border none
          border-radius .05rem
      button.el-button
        width .8rem
        height .8rem
        border none 
        border-radius .4rem
        box-shadow 0px 2px 3px grey
        position absolute
        left 50%
        transform translate(-50%, 0)
        background-color rgba(255,255,255,0.5)
        font-size .3rem
        color grey
    .register
      position absolute
      top 90%
      font-size .2rem
      width 100%
      .register_c
        position absolute
        transform translate(-50%)
        left 50%
        width 100%
        font-size .1rem
        color #ffffff
        text-align center
        text-decoration underline 
</style>


