<template>
  <div class="my-c">
    <div class="img-all">
      <div class="img-box">
        <img src="@/assets/pic/test-img/2.png" alt="头像">
      </div>
      <div class="user-name">北北北不是南</div>
      <div class="set-link iconfont icongengduo"></div>
    </div>
    <div class="button-c">
      <div class="item-box">
        <el-button class="button-item fan" round><p>我的粉丝<br>20</p></el-button>
      </div>
      <div class="item-box">
        <el-button class="button-item focus" round><p>我的关注<br>20</p></el-button>
      </div>
      <div class="item-box">
        <el-button class="button-item like" round><p>收到喜欢<br>20</p></el-button>
      </div>
    </div>
    <div class="list-c">
      <el-button class="list-item iconfont iconshu" round style="margin-left:0">forest</el-button>
      <el-button class="list-item iconfont iconpinglun" round style="margin-left:0">评论</el-button>
      <el-button class="list-item iconfont iconshoucang1" round style="margin-left:0">点过的赞</el-button>
      <el-button class="list-item iconfont iconshoucang" round style="margin-left:0">收藏夹</el-button>
    </div>
    <div class="logout">
      <el-button class="logout-button" v-on:click="logout">退出登录</el-button>
    </div>
    <tab-bar class="app-bottom"></tab-bar>
  </div>
</template>

<script>
import tabBar from './tabbar/tabBar.vue'
export default {
  name: 'my',
  components: {
    tabBar
  },
  methods: {
    logout () {
      window.sessionStorage.clear()
      this.$router.push('/')
    }
  }
}
</script>

<style lang="stylus" scoped>
  .my-c
    background: url('~@/assets/pic/my.png') center center no-repeat
    background-size 100% 100%
    background-color rgb(99,148,183)
    width 100%
    height 100%
    font-size .5rem
    .img-all
      position relative
      top 7%
      width 100%
      height .92rem
      display flex
      .img-box
        padding-top .11rem
        padding-left .2rem
        width .7rem
        height .7rem
        border-radius .35rem
        overflow hidden
      .user-name
        font-size .25rem
        flex 1
        padding-left .2rem
        padding-top .35rem
      .set-link
        font-size .25rem
        font-weight bold
        padding-right .2rem
        padding-top .35rem
        color rgb(205,205,205)
    .button-c
      position relative
      top 10%
      width 100%
      height .67rem
      display flex
      .item-box
        width 30%
        padding-left .1rem
        padding-right .15rem
        display flex
        flex 1
        .button-item
          height .6rem
          width 100%
          border-radius .3rem
          text-align center
          border none 
          box-shadow 0px 3px 5px rgb(205,205,205)
        .fan
          background-color rgb(255,246,236)
        .focus
          background-color rgb(240,245,248)
        .like
          background-color rgb(252,237,244)
    .list-c
      position relative
      top 10%
      width 100%
      height 2.48rem
      padding 0rem .2rem
      box-sizing border-box
      .list-item
        width 100%
        height .45rem
        box-sizing border-box
        text-align left 
        font-size .2rem
        color rgb(205,205,205)
        background-color rgba(255,255,255,0.4)
        box-shadow 0px 3px 5px rgb(205,205,205)
    .logout
      position fixed
      bottom 10%
      text-align center
      width 100%
      .logout-button
        width 100%
        border-radius 0px
        color red
        background-color rgb(247,249,245)
    .app-bottom
      position fixed
      bottom 0px
      z-index 99
      width 100%
</style>


